export default {
	data() {
		return {
			id:'',
			type:'',
			detail:''
		}
	},
	onLoad(e) {
		this.id = e.id
		this.type = e.type
		this.getData()
	},
	methods: {
		aaa(){
			uni.navigateTo({
				url:'../../pagesTwo/MyBusiness/MyBusiness?id='+this.detail.company_id
			})
		},
		getData(){
			this.$api.pro_ser_detail({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				id:this.id,
				type:this.type
			}).then(res=>{
				console.log(res)
				this.detail = res.data.data
			})
			
		}
	}
}
